
.switch-wrapper {
  position: relative;
  width: 30px;
  height: 15px;
  border-radius: 50px;
}
.switch-wrapper.off {
  background: #ccc;
}
.switch-wrapper.on {
  background: #008df2;
}
.switch-wrapper .cursor {
  width: 55%;
  height: 100%;
  background: #fff;
  border-radius: 100%;
  position: absolute;
  left: 0px;
  top:-1px;
  border: 1px solid #eee;
  -webkit-transition: left .2s;
  transition: left .2s;
  cursor: pointer;
}
.switch-wrapper.on .cursor {
  left: 12px;
}



